@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,700;1,400;1,800&display=swap');
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body {
  font-family: 'Poppins', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: hsl(0, 0%, 94%);
}
.App {
  min-height: 70vh;
  min-width: 750px;
  padding: 40px;
  border-radius: 20px 20px 200px;
  background-color: white;
  box-shadow: rgba(100, 100, 111, 0.1) 0px 7px 29px 0px;
}
@media (max-width: 767px) {
  .App {
    min-width: calc(100% - 40px);
    margin-left: 20px;
    margin-right: 20px;
    padding: 20px;
  }
}
.App .age-inputs {
  display: flex;
  gap: 25px;
  border-bottom: 1px solid hsl(0, 0%, 86%);
  padding-bottom: 30px;
}
@media (max-width: 767px) {
  .App .age-inputs {
    gap: 15px;
  }
}
.App .age-inputs .input {
  display: flex;
  flex-direction: column;
}
.App .age-inputs .input label {
  color: hsl(0, 1%, 44%);
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 2px;
  margin-bottom: 10px;
}
.App .age-inputs .input label.warning {
  color: hsl(0, 100%, 67%);
}
.App .age-inputs .input input {
  font-size: 32px;
  padding: 10px 20px;
  width: 150px;
  border-radius: 8px;
  border: 1px solid hsl(0, 0%, 86%);
  margin-bottom: 10px;
  font-weight: 700;
}
@media (max-width: 767px) {
  .App .age-inputs .input input {
    width: 100%;
    padding: 10px;
    font-size: 24px;
  }
}
.App .age-inputs .input input.warning {
  border-color: hsl(0, 100%, 67%);
}
.App .age-inputs .input input::placeholder {
  color: hsl(0, 1%, 44%);
  font-weight: 700;
  text-transform: uppercase;
  transition: 0.3s;
}
.App .age-inputs .input input:focus {
  outline: 1px solid hsl(259, 100%, 65%);
}
.App .age-inputs .input input:focus::placeholder {
  opacity: 0;
}
.App .age-inputs .input .message {
  color: hsl(0, 100%, 67%);
  font-size: 12px;
  font-weight: 400;
  font-style: italic;
  height: 20px;
}
.App .submit {
  background-color: hsl(259, 100%, 65%);
  border: none;
  border-radius: 50%;
  padding: 20px;
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: 0.3s;
  position: relative;
  width: 80px;
  height: 80px;
  top: -40px;
}
@media (max-width: 767px) {
  .App .submit {
    margin: 0 auto;
  }
}
.App .submit:hover {
  background-color: black;
}
.App .age {
  margin-top: -20px;
}
.App .age > div {
  font-size: 70px;
  font-style: italic;
  font-weight: 800;
}
@media (max-width: 767px) {
  .App .age > div {
    font-size: 50px;
  }
}
.App .age > div span {
  color: hsl(259, 100%, 65%);
  margin-right: 10px;
}
